@import "../../../../styles/animations";

.weekly {
	grid-row: 2;
	grid-column: 2 / span 3;
	display: flex;
	justify-content: space-around;
	align-items: center;
	gap: 1px;
	overflow: scroll;
	padding: 0;
	&_day {
		width: 100%;
		height: 100%;
		position: relative;
		padding: 10px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		background-color: var(--var-tertiary-color);
		cursor: pointer;
		&::before {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			transition: 0.2s ease-in;
			border-radius: 3px;
			background: linear-gradient(
				141deg,
				rgba(187, 193, 248, 1) 27%,
				rgba(110, 218, 243, 1) 77%,
				rgba(0, 255, 235, 1) 100%
			);
		}
		&:hover {
			border-radius: 3px;

			&::before {
				opacity: 1;
			}
		}

		&_description {
			display: flex;
			flex-direction: column;
			align-items: center;
			span {
				height: 50px;
				display: flex;
				align-items: center;
				text-align: center;
				opacity: 0.5;
			}
		}
	}
}
.index {
	z-index: 999;
}
.popup {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 999;
	display: grid;
	place-items: center;
	background: transparent;
	backdrop-filter: blur(5px);
	animation: appears_popup 0.1s linear;
	table {
		width: 80%;
		height: 50%;
		background: var(--var-primary-color);
		border-radius: 10px;
	}
	&_close-btn {
		position: absolute;
		top: 10px;
		right: 10px;
		width: 50px;
		height: 50px;
		background: transparent;
		svg {
			fill: white;
			width: 100%;
			height: 100%;
			opacity: 0.5;
			transition: 0.1s ease-in;
		}
		&:hover {
			svg {
				opacity: 1;
			}
		}
	}
}
